@import "./src/assets/sass/prepends.scss";

.card-wrapper {
  padding: 0.5rem;
  margin: 0;
  align-items: stretch;

  :hover {
    text-decoration: none;
    background-color: #e4f4f8;
    h4 {
      text-decoration: underline;
    }
  }

  .card-sound {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 0.5rem;
    margin: 0;
    > * {
      display: block;
      width: 100%;
      margin: 0.25rem 0 0;
    }

    time {
      text-align: right;
    }
    .songdetail {
      flex-grow: 1;

      span {
        margin-right: 1rem;
      }
    }
  }
}
