@import "./src/assets/sass/prepends.scss";

#wave-wrapper {
  margin: 0;
  padding: 1rem;
  position: relative;

  h4#songtitle {
    margin-bottom: 1.5rem;
  }

  #controller {
    margin: 1rem auto;
    button {
      display: inline-block;
      box-sizing: border-box;
      padding: 0.5rem;
      margin: 0;
      font-size: 1.25rem;
      background-color: transparent;
      border: 1px solid $text-dark;
      color: $text-black;
    }
  }

  #songlist {
    .song-row {
      display: flex;
      padding: 0.5rem;
      margin: 0 -0.5rem;
      cursor: pointer;
      align-items: center;

      > * {
        display: inline-block;
      }
      .songnum {
        margin-right: 0.5rem;
      }
      .songname {
        flex: 1 1 auto;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .download {
        padding: 0.25rem 0.5rem;
        font-size: 1.2rem;
        border: 1px solid transparent;
        border-radius: 0.25rem;
        &:hover {
          border-color: $text-dark;
        }
      }
      .songtime {
        width: 3rem;
        text-align: center;
      }
    }
  }

  #wavetimeline {
    position: relative;
    display: block;
    background-color: rgba(255, 255, 255, 0.3);
  }
  #waveloading {
    position: absolute;
  }
  #timedisplay {
    position: absolute;
    height: 18px;
    width: 110px;
    line-height: 16px;
    padding: 1px 2px;
    margin: 110px 0 0 auto;
    text-align: center;
    font-size: 16px;
    background-color: #333;
    color: #eee;
    z-index: 8;
  }
}
